import Modal from '@material-ui/core/Modal';
import firebase from 'firebase/app';
import * as React from 'react';
import Wrapper from './FoodistPageStyled';

const FoodistPage = () => {
  const [imageSrc, setImageSrc] = React.useState('');
  const [lastUpdate, setLastUpdate] = React.useState(new Date());
  const [open, setOpen] = React.useState(true);
  React.useEffect(() => {
    const database = firebase.database();
    database.ref('images/').once('value').then(snapshot => {
      const img = (snapshot.val() && snapshot.val().imageSrc) || '';
      const lastUp = (snapshot.val() && snapshot.val().lastUpdate) || '';
      setImageSrc(img);
      setLastUpdate(new Date(lastUp));
    })
  }, []);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <Wrapper>
      <button type="button" onClick={handleOpen}>
        
      </button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        <div style={{
          background: 'white',
          marginTop: '200px',
          color: 'red',
          fontSize: '30px',
        }}>
           :     .    <br />
          <a href="https://office-htw.web.app/">   .</a>
        </div>
      </Modal>
      {lastUpdate && (
        <div className="last-update"> : {lastUpdate.toString()}</div>
      )}
      <img style={{ width: '100%' }} src={imageSrc} />
    </Wrapper>
  )
}

export default FoodistPage;



